/*      light colors         */
@keyframes spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@-webkit-keyframes spin {
	from {
		-webkit-transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
	}
}

/*           Font Smoothing      */

body,
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6,
p,
.navbar,
.brand,
.btn-simple,
.alert,
a,
.td-name,
td,
button.close {
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	font-family: "Roboto", "Helvetica Neue", Arial, sans-serif;
	font-weight: 400;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4 {
	font-weight: 300;
	margin: 30px 0 15px;
}

h1,
.h1 {
	font-size: 52px;
}

h2,
.h2 {
	font-size: 36px;
}

h3,
.h3 {
	font-size: 28px;
	margin: 20px 0 10px;
}

h4,
.h4 {
	font-size: 22px;
	line-height: 30px;
}

h5,
.h5 {
	font-size: 16px;
	margin-bottom: 15px;
}

h6,
.h6 {
	font-size: 14px;
	font-weight: 600;
	text-transform: uppercase;
}

p {
	font-size: 16px;
	line-height: 1.5;
}

h1 small,
h2 small,
h3 small,
h4 small,
h5 small,
h6 small,
.h1 small,
.h2 small,
.h3 small,
.h4 small,
.h5 small,
.h6 small,
h1 .small,
h2 .small,
h3 .small,
h4 .small,
h5 .small,
