/*     General overwrite     */

body,
.wrapper {
	min-height: 100vh;
	position: relative;
	background-color: white;
}

a {
	color: #1DC7EA;
}

a:hover,
a:focus {
	color: #42d0ed;
	text-decoration: none;
}

a:focus,
a:active,
button::-moz-focus-inner,
input::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
